<template>
  <div id="uploadImage">
    <div class="main">
      <div class="tt">教学信息</div> 
      <el-form :model="form" :rules="rules" style="margin-top:60px;width:60%;" ref="form">
        <el-form-item label="教工号:" :label-width="formLabelWidth" prop="TeacherNo"> 
            <el-input :disabled="true" v-model="form.TeacherNo" clearable></el-input> 
        </el-form-item> 
        <el-form-item label="卡编号:" :label-width="formLabelWidth" prop="TeacherCardNo"> 
            <el-input :disabled="true" v-model="form.TeacherCardNo" clearable></el-input> 
        </el-form-item> 
        <el-form-item label="是否在编：" :label-width="formLabelWidth" prop="STInService" :loading="true">
          <el-input :disabled="true" v-model="form.STInService" clearable></el-input> 
            </el-form-item>
            <el-form-item label="是否名师：" :label-width="formLabelWidth" prop="STFamous" :loading="true"> 
              <el-input :disabled="true" v-model="form.STFamous" clearable></el-input> 
            </el-form-item>
             <el-form-item label="进校年月：" :label-width="formLabelWidth" prop="InToDate" placement="right"> 
              <el-input :disabled="true" v-model="form.InToDate" clearable></el-input> 
            </el-form-item>  
            <el-form-item label="所属部门：" :label-width="formLabelWidth" prop="DeptTeacher" :loading="true">
              <el-input :disabled="true" v-model="form.DeptTeacher" ></el-input> 
            </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button :disabled="true" :loading="createLoading1" type="primary" @click="comfirmFormHandler('form')">
          <i class="el-icon-check el-icon--left"></i>保 存
        </el-button>
        <el-button :disabled="true" @click="resetForm">
          <i class="el-icon-close el-icon--left"></i>重 置
        </el-button>
      </div> 
    </div>
  </div>
</template>

<script> 
import http from "../../../../utils/http";
import api from "../../../../utils/api";
export default {
  name: "",
  components: {},
  props: {},
  data() {
    return { 
      formLabelWidth: "160px", 
      createLoading1: false,
      form: {
        TeacherNo: "",
        TeacherCardNo: "",
        STInService: "",
        STFamous: "",
        InToDate: "",
        DeptTeacher: ""
      },  
      userid:"",
      username:"",
      rules: {}
    };
  },
  computed: {}, 
  mounted() {
    let userInfo = http.getUser();
    if (userInfo) {
        this.userid = userInfo.UserID;
        this.username = userInfo.UserName;
      }
    this.getdata();
  },
  methods: {
    async getdata() {
      const res = await http.get(api.getTeacheringInfo, {
          uid: this.userid,
          Token: http.getToken()
        });
        if (!res.Result) {
          this.$message({
            showClose: true,
            message: res.message,
            type: res.type
          });
        } else {
          if(res.Data != null) {
             this.form.TeacherNo = res.Data.ThcNo;
             this.form.TeacherCardNo = res.Data.ThcCardNo;
             this.form.STInService = res.Data.isinserice;
             this.form.STFamous = res.Data.STFamous;
             this.form.InToDate = res.Data.InToDate;
             this.form.DeptTeacher = res.Data.thlDept;
          }
        }
    },
    resetForm() {
      this.$refs["form"].resetFields();
      this.$refs.form.clearValidate();
    }
  }
};
</script>

<style lang='less'>
#uploadImage {
  text-align: left;
  width: 956px;
  height: 1017px;
  background: rgba(255, 255, 255, 1);
  border-radius: 3px;
  //
  padding: 20px;
  padding-top: 0;
  .tt {
      color: #333;
      font-size: 14px;
      border-bottom: 1px solid #ecf1f7;
      line-height: 50px;
    } 
  .main {  
      .dialog-footer {
        width: 100%;
        display: block;
        text-align: center;
        margin-top: 36px;

    }
  }
}
</style>


